.dialogs{
    display: grid;
    grid-template-columns: 2fr 10fr;
}
.dialogsItem{
    display: grid;
}
.dialogs a{
    text-decoration: none;
    color: gray;
    padding: 10px;
}
.messages{
    display: grid;
}
.message{
    padding: 10px;
}
.dialogsItem .active a{
    color: red;
}